//header
.carChargeStatus{
    height: 30px;
    display:flex;
    justify-content: space-between;
    a{
        text-decoration: none;
        color:black;
    }
    button{
        width: 10%;
        background-color: white;
        
    }
    .carChargeStatus__home{
        display:flex;
        align-items: center;
        justify-content: center;
        border:1px solid black;
        width: 20%;
    }
    .carChargeStatus__stautus{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 80%;
        border:1px solid black;
    } 
}

//main
.carChargeSection{
    .carChargeSection__box{
        margin-top:40px;
        border:1px solid black;
        display:flex;
        .carChargeSection__img{
            width: 150px;height: 150px;
            border:1px solid blue;
        }
        div{
            border:1px solid black;
            p{
                line-height: 25px;
                margin-left:8px;
            }
        }
        .carChargeSection__detail{
            text-align: center;
            display: flex;
            align-items: center;
        }
    }
}