.faqheader{
    height: 30px;
    display:flex;
    justify-content: space-between;
    border:1px solid black;
    
    button{
        width: 10%;
        background-color: white;
        
    }
    .faqheader__home{
        display:flex;
        align-items: center;
        justify-content: center;
        border:1px solid black;
        width: 20%;
    }
    .fqaheader__faq{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 80%;
        border:1px solid black;
    } 
}

//FaqNotice
.faqsection{
    .faqsection__box{
        width: 90%;
        margin:0 auto;
        margin-top:46px;
        div{
            height: 43px;
            margin-top:10px;
            border:1px solid black;
            font-size:15px;
            p{
                margin-top:5px;
                margin-left:10px;
            }
            span{
                margin-left:10px;
            }
        }
    }
}