
    //MainStatus
.MainStatus{
    font-weight: bold;
    background-color: #f6f6f6;

    width: 90%;
    height: 270px;
    margin:0 auto;
    margin-top:10px;
    margin-bottom:20px;
    border-radius: 10px;
    a{
        text-decoration: none;
        color:black;
    }
    .section__MainStatus{  
        height: 80%;
        margin:0 auto;
        margin-top:40px;
        .section__MainStatus__box{
            padding:14px 0; 
            .section__MainStatus__situation{
                margin:2px 20px;
            }

            .a{
                display:flex;
                align-items: center;
                justify-content: center;
                .section__MainStatus__information{
                    width: 150px;height: 150px;
                    border:1px solid black;
                    border-radius: 10px;
                    margin:14px 18px;
                }
                .section__MainStatus__menu{
                    ul{
                        li{
                            line-height: 20px;
                        }
                    }
                }
            }
            
            
            .section__MainStatus__btn{
                display:flex;;
                justify-content: center;
                div{
                    width: 35%; height: 35px;
                    border-radius: 10px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    background-color: #1fb04d;
                    a{
                        color:white;
                    }
                }
                div:nth-child(1){
                    margin-right:50px;
                }
            }
     }
    }
    
}
 /* MainFavorites */
 .MainFavorites{
    font-weight:bold;
     .section__favorites{
        width: 90%;
        height: 350px;
        border:none;
        border-top:none;
         border-radius: 10px;
    background-color: #f6f6f6;

         margin:0 auto;
         hr{
            width: 90%;
         }
         .section__favorites__box{
            opacity: 0.8;
            height: 30px;
            line-height: 29px;
            border-left:none;
            border-right:none;
            display:flex;
             div{
                margin:0 auto; 
                border-radius: 10px;
                width: 100%;
                text-align: center;
             }
             
         }
        
     }
     .section__favorites__box2{
         display:flex;
         margin-top:7px;
         justify-content: space-around;
        
     }
 }
 
 /* MainCarSearch */

 .MainCarSearch{
            margin-top:20px;
            width: 100%;
            
            
     a{
         text-decoration: none;
         color:black;
        
        }
     .MainCarSearch__box{
            display:grid;
            grid-template-columns: repeat(2,1fr);
            color:white;
            font-size:27px;
            background-color: #5193b6;
         .a{
             width: 60%;
             display:flex;
             margin:0 auto;
             margin-top:13px;
             align-items: center;
             div{
                 background-color: white;
                 width: 100%;
                 height: 40px;
                 border:1px solid white;
                 font-size:10px;
                margin-left:10px;
                font-weight: bold;
                display:flex;
                align-items: center;
                justify-content: center;       
             }
         }
         .MainCarSearch__box__menu{
             text-align: center;
             padding-top:20px;
             width: 100%; height: 100px;
            border:1px solid black;
            }
         .MainCarSearch__box__use{
                width: 100%; height: 100px;
                  text-align: center;
                padding-top:20px;
                line-height: 80px;   
             div{
                 a{
                     color:white;
                 }
             }
             
             
         }
     }
 }
 
 /* MainNotice */
 .MainNotice{
    a{
        text-decoration: none;
        color:black;
       
       }
     .MainNotice__box{
         display:flex;
         border:1px solid black;
         div:nth-child(1){
            width: 32%;
        }
         div{
             display:flex;
             justify-content: space-around;
             align-items: center;
             border:1px solid black;
             width: 100%;
             text-align: center;
             height: 52px;
             
         }
     }
 }

