.paymentView__section{
    margin:0 auto;
    margin-top:30px;
    .paymentView__box{
        height: 300px;
        border:1px solid blue;
        display:flex;
        position: relative;
        .paymentView__click{
            border:1px solid blue;
            position: absolute;
            right:0;
            height: 100%;
        }
        .paymentView__choice{
            border:1px solid blue;
            
        }
        .a{
            height: 50px;
            margin:30px;
            margin-left:80px;
            display:flex;
            .b{
                width: 90px;
                margin-right:20px;
            }
        }
        .paymentView__text{
            width: 50%;
            height: 50px;
            position:absolute;
            bottom:100px;
            left:100px;
        }
    }
    .btn{
        button{
            background-color: white;
            position: absolute;
            bottom:0;
            height: 50px;
            width: 100%;
        }
    }
}