//header
.carDetail{
    height: 30px;
    display:flex;
    justify-content: space-between;
    a{
        text-decoration: none;
        color:black;
    }
    button{
        width: 10%;
        background-color: white;
        
    }
    .carDetail__home{
        display:flex;
        align-items: center;
        justify-content: center;
        border:1px solid black;
        width: 20%;
    }
    .carDetail__detail{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 80%;
        border:1px solid black;
    } 
}

// 차 정보(차량번호, 예약시간, 충전시간 등등..)
.carDetailSection{
    .carDetailSection__box{
        .carDetailSection__img{
            width: 90%;height: 260px;
            border:1px solid blue;
            margin:0 auto;
            margin-top:40px;
        }
        .carDetailSection__information{
            margin-top:12px;
            margin-left:19px;
            p{
                margin-bottom:8px;
            }
        }
    }
}

//충전 예약취소, 종료버튼
.carDetailCancel{
    width: 100%;
    position: fixed;
    bottom:0;
    
    .carDetailCancel__box{
        
        button{
            height: 40px;
            width: 50%;
            background-color: white;
        }    
    }
}