.searchHeader{
    height: 30px;
    display:flex;
    justify-content: space-between;
    border:1px solid black;
    a{
        text-decoration: none;
        color:black;
    }
    button{
        width: 10%;
        background-color: white;
        
    }
    .searchHeader__home{
        display:flex;
        align-items: center;
        justify-content: center;
        border:1px solid black;
        width: 20%;
    }
    .searchHeader__search{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 80%;
        border:1px solid black;
    } 
}

// search
.searchSection{
    .search__box{
        form{
            input{
                height: 50px;
            }
            input[type="text"]{
                width: 60%;
            }
            input[type="submit"]{
                width: 38%;
            }
        }
    }
}