.accountheader{
    height: 30px;
    display:flex;
    justify-content: space-between;
    a{
        text-decoration: none;
        color:black;
    }
    button{
        width: 10%;
        background-color: white;
        
    }
    .accountheader__home{
        display:flex;
        align-items: center;
        justify-content: center;
        border:1px solid black;
        width: 20%;
    }
    .accountheader__account{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 80%;
        border:1px solid black;
    } 
}

//Main account
.account{
    margin-top:100px;
    a{
        text-decoration: none;
        color:blue;
    }    
    .account__box{
        margin-top:30px;
        form{
            display:flex;
            flex-direction: column;
            width: 70%;
            margin: 0 auto;
            input{
                height: 46px;
            }
            input[type="submit"]{
                margin-top:20px;
                background-color: aliceblue;
                border:1px solid aliceblue;
            }
        }
        .account__login{
            margin:17px 0;
            text-align: center;
        }
    }
}