.mycarheader{
    height: 30px;
    display:flex;
    justify-content: space-between;
    a{
        text-decoration: none;

    }
    button{
        width: 10%;
        background-color: white;
        border:none;
        svg{
            font-size:24px;
        }        
    }
    .mycarheader__home{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 20%;
    }
    .mycarheader__mycar{
        width: 80%;
        display:flex;
        align-items: center;
        justify-content: center;
    } 
}


main{
    a{
        color:black;
        text-decoration: none;
    }
    section{
        .mycar{
            width: 90%; height: 240px;
            background-color: #f6f6f6;
            border-radius: 10px;
            margin:0 auto;
            margin-top:20px;
            display:flex;
            .mycar__choice{
                width: 40px;
            border-radius: 10px;
                
                height: 100%;
                background-color: #1fb04d;
                display:flex;
                flex-direction: column;
                justify-content: center;
                text-align: center;
                a{
                    color:white;
                }
            }
            .mycar__img{
                border:1px solid black;
                width: 20%;
                height: 80%;
                margin:20px;
            }
            .mycar__text{
                width: 205px;
                margin:0 auto;
                display:flex;
                flex-direction: column;
                justify-content:center;
                p{
                    margin-bottom:10px;
                }
                div{
                    border:1px solid black;

                    width: 90%;
                    height: 10%;
                    padding-top:5px;
                    margin: 0 auto;
                    position:relative;
                    top:28px;
                    text-align: center;
                }
            }
            .mycar__correction{
            border-radius: 10px;

                width: 10%;
                display:flex;
                justify-content: center;
                align-items: center;
                background-color: #1fb04d;
                a{
                    color:white;
                }
            }
        }
        .mycar__registration{
            width: 100%;
            height: 40px;
            border:1px solid black;

            position: fixed;
            bottom:0;
            text-align: center;
            line-height: 40px;
        }
    }
}