.paymentRegistration__header{
    height: 30px;
    display:flex;
    justify-content: space-between;
    a{
        text-decoration: none;
        color:black;
    }
    button{
        width: 10%;
        background-color: white;
        
    }
    .payment__home{
        display:flex;
        align-items: center;
        justify-content: center;
        border:1px solid black;
        width: 20%;
    }
    .payment__view{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 80%;
        border:1px solid black;
    } 
}

