.header{
    background-color: white;
    nav{
            a{
                text-decoration: none;
            }
        .header__ul{
            display:flex;
            height: 30px;
            font-weight: bold;
            .header__menu{
                text-align: center;

                line-height: 25px;
                img{
                    width: 100%;
                    height: 28px;
                }
            }
            .menu__click{
                width: 15%;   
                line-height: 25px;
                transition:.5s;
                cursor: pointer;
                .menu__btn{
                    margin-top:12px;
                    position: relative;
                    z-index:1;
                    background-color: white;
                    width: 100vh;
                    height: 700px;
                    > li:nth-child(2){
                        margin-top:30px;
                    }
                    li{
                        width: 100%;
                        border:none;
                        ul{
                            .myPageList{
                                margin-left:15px;
                                .myPageList__li{
                                    color:black;
                                    opacity: 0.4;
                                }
                            }
                        }
                    }
                    .loginbox{
                        position: absolute;
                        bottom: 0;
                    }
                }
            }
            li:nth-child(2){
                width: 30%;
            }li:nth-child(3){
                width: 60%;
            }li:nth-child(4){
                width: 15%;
            }
        }
        }
    }
