
//Login header
.loginheader{
    height: 30px;
    display:flex;
    justify-content: space-between;
    a{
        text-decoration: none;
    }
    button{
        width: 10%;
        background-color: white;
        border:none;
        svg{
            font-size:20px;
        }
    }
    .loginheader__home{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 20%;
    }
    .loginheader__login{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 80%;
    } 
}


//Main Login 
.Login{
    a{
        text-decoration: none;
        color:black;
    }
    .Login__form__box{

        .Login__form{
            display:flex;
            flex-direction: column;
            width: 70%;
            margin:0 auto;
            margin-top:100px;
            .img__logo{
                width:300px; height: 100px;
                margin-bottom:24px;
            }
            input{
                height: 46px;
            }
            input[type="submit"]{
                margin-top:20px;
                background-color: skyblue;
                color:white;
                border:none;
                border-radius: 30px;
            }
        }
        .Login__users__account{
            margin:17px 0;
            display:flex;
            justify-content: center;
            div{
                margin-right:18px;
            }
        }
    }
    //Main SocialLogin 
    .Login__users__socialLogin{
        text-align: center;
        margin:0 auto;
        margin-top:80px;
        div{
            //박스 크기,위치중간으로 맞춤
            width: 53%;
            height: 43px;
            margin:0 auto;
            margin-bottom:14px;
            a{
                color:white;
            }

            // 글씨 배열 상하좌우 맞춤
            display:flex;
            align-items: center;
            justify-content: center;
        }
        .Login__users__kakaotalk{
            background-color: #fae100;
        }
        .Login__users__naver{
            background-color: #19ce60;
        }
    }
}