//MainCar Img
.MainCarImg__section{
    div{
        width: 350px;height: 200px;
        border:1px solid blue;
        margin:0 auto;
        margin-top:20px;
    }   
}

//MainCarRegistranion
.MainCarRegistration__section{
    margin-top:20px;
    .MainCarRegistration__box{
        .MainCarRegistration__choice{
            margin:20px 20px;
            text-align: center;
            p{
                margin-bottom:5px;
                font-size:19px;
            }
            select{
                width: 170px;
                height: 30px;
                text-align: center;
            }
        }
    }
}

//MacinCarConnector
.MainCarConnector__section{
    .MainCarConnector__box{
        .MainCarConnector__choice{
            text-align: center;
            .MainCarConnector__menu{
                margin:0 auto;
                margin-top:10px;
                width: 80%;
                display:grid;
                grid-template-columns: repeat(3, 1fr);
                div{
                    border:1px solid blue;
                    width: 71%;
                    height: 30px;
                    margin:10px 20px;
                    line-height: 30px;
                    font-size:13px;
                }
            }
        }
    }
}


//MainCarNumber 
.MainCarNumber__section{
    margin-top:20px;
    .MainCarNumber__box{
        .MainCarNumber__search{
            text-align: center;
            p{
                margin-bottom:10px;
            }
            input{
                width: 60%;
                height: 37px;
                text-align: center;
            }
       
            input[type="submit"]{
                width: 100%;
                padding:15px 0;
                position: absolute;
                bottom:0; left:0;
                line-height: 15px;
                background-color: white;
                border:1px solid blue
            }
        }
    }
}